import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Paper, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow,
  TablePagination,
  Chip,
  Divider,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid
} from '@mui/material';
import axiosInstance from '../api/axios';
import { formatDistanceToNow } from 'date-fns';
import { AccessTime as TimeIcon } from '@mui/icons-material';

const LearningExamples = ({ daysAgo, onDateFilterChange }) => {
  const [examples, setExamples] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  useEffect(() => {
    const fetchLearningExamples = async () => {
      try {
        setLoading(true);
        const params = daysAgo ? { days_ago: daysAgo } : {};
        const response = await axiosInstance.get('/api/feedback/learning-examples', { params });
        if (response.data.success) {
          setExamples(response.data.examples);
        } else {
          setError(response.data.error || 'Failed to fetch learning examples');
        }
      } catch (err) {
        setError(err.message || 'An error occurred while fetching learning examples');
      } finally {
        setLoading(false);
      }
    };

    fetchLearningExamples();
  }, [daysAgo]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const formatTimestamp = (timestamp) => {
    try {
      return formatDistanceToNow(new Date(timestamp), { addSuffix: true });
    } catch (e) {
      return 'Unknown time';
    }
  };

  // Get date filter label
  const getDateFilterLabel = () => {
    if (daysAgo === null) return 'All Time';
    if (daysAgo === 7) return 'Last 7 Days';
    if (daysAgo === 14) return 'Last 14 Days';
    if (daysAgo === 30) return 'Last 30 Days';
    return `Last ${daysAgo} Days`;
  };

  if (loading) {
    return (
      <Box sx={{ p: 3 }}>
        <Typography>Loading learning examples...</Typography>
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ p: 3 }}>
        <Alert severity="error">{error}</Alert>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Grid container spacing={2} alignItems="center" sx={{ mb: 3 }}>
        <Grid item xs={12} md={8}>
          <Typography variant="h6" gutterBottom>
            Learning Examples ({examples.length})
          </Typography>
          <Typography variant="body2" color="text.secondary">
            These examples are collected from your corrections and will be used to improve the AI clone's responses.
          </Typography>
        </Grid>
        <Grid item xs={12} md={4}>
          <FormControl fullWidth variant="outlined" size="small">
            <InputLabel>Time Period</InputLabel>
            <Select
              value={daysAgo === null ? 'all' : daysAgo.toString()}
              label="Time Period"
              onChange={(e) => {
                const value = e.target.value;
                onDateFilterChange(value === 'all' ? null : parseInt(value));
              }}
            >
              <MenuItem value="all">All Time</MenuItem>
              <MenuItem value="7">Last 7 Days</MenuItem>
              <MenuItem value="14">Last 14 Days</MenuItem>
              <MenuItem value="30">Last 30 Days</MenuItem>
              <MenuItem value="60">Last 60 Days</MenuItem>
            </Select>
          </FormControl>
        </Grid>
      </Grid>
      
      <Chip 
        icon={<TimeIcon />} 
        label={getDateFilterLabel()} 
        color="default" 
        variant="outlined"
        sx={{ mb: 2 }}
      />
      
      <Divider sx={{ my: 2 }} />
      
      {examples.length === 0 ? (
        <Alert severity="info">
          No learning examples available for the selected time period. When you provide corrections to AI responses, 
          they will appear here to help improve the model.
        </Alert>
      ) : (
        <>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="learning examples table">
              <TableHead>
                <TableRow>
                  <TableCell>Original Response</TableCell>
                  <TableCell>Your Correction</TableCell>
                  <TableCell>Channel</TableCell>
                  <TableCell>Time</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {examples
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((example, index) => (
                    <TableRow key={index}>
                      <TableCell sx={{ maxWidth: 300, whiteSpace: 'normal', wordBreak: 'break-word' }}>
                        {example.original}
                      </TableCell>
                      <TableCell sx={{ maxWidth: 300, whiteSpace: 'normal', wordBreak: 'break-word' }}>
                        {example.corrected}
                      </TableCell>
                      <TableCell>
                        <Chip 
                          label={example.channel === 'email' ? 'Email' : 'Text Message'} 
                          size="small" 
                          color={example.channel === 'email' ? 'primary' : 'secondary'}
                        />
                      </TableCell>
                      <TableCell>{formatTimestamp(example.timestamp)}</TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={examples.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </>
      )}
    </Box>
  );
};

export default LearningExamples;
