import React, { useState, useEffect } from 'react';
import { Button, CircularProgress, Typography, Box, Alert } from '@mui/material';
import EmailIcon from '@mui/icons-material/Email';
import axios from '../api/axios';

/**
 * Gmail OAuth Button Component
 * 
 * This component provides a button for users to authenticate with Gmail
 * using OAuth, allowing the AI clone to access their emails for training
 * and autoresponse functionality.
 */
const GmailOAuthButton = ({ onAuthSuccess, userId = 'default' }) => {
  const [loading, setLoading] = useState(false);
  const [authStatus, setAuthStatus] = useState({ authenticated: false, message: '' });
  const [error, setError] = useState('');

  // Check if the user is already authenticated with Gmail
  useEffect(() => {
    checkAuthStatus();
  }, []);

  // Check authentication status
  const checkAuthStatus = async () => {
    try {
      const response = await axios.get(`http://localhost:5002/api/oauth/status/gmail?user_id=${userId}`);
      setAuthStatus(response.data);
    } catch (error) {
      console.error('Error checking Gmail auth status:', error);
      setError('Failed to check Gmail authentication status.');
    }
  };

  // Handle OAuth result when user returns from Google auth page
  useEffect(() => {
    const handleOAuthResult = () => {
      const urlParams = new URLSearchParams(window.location.search);
      const status = urlParams.get('status');
      const service = urlParams.get('service');
      
      if (service === 'gmail') {
        if (status === 'success') {
          setAuthStatus({ authenticated: true, message: 'Successfully authenticated with Gmail!' });
          if (onAuthSuccess) onAuthSuccess();
        } else if (status === 'error') {
          setError('Failed to authenticate with Gmail. Please try again.');
        }
        
        // Clean up URL parameters
        window.history.replaceState({}, document.title, window.location.pathname);
        
        // Refresh auth status
        checkAuthStatus();
      }
    };
    
    handleOAuthResult();
  }, [onAuthSuccess]);

  // Start the OAuth flow
  const handleAuth = async () => {
    setLoading(true);
    setError('');
    
    try {
      // Use the authorized redirect URI from Google Cloud Console
      const redirectUri = `http://localhost:5002/api/oauth/callback/gmail`;
      
      // Redirect to the OAuth authorization URL
      window.location.href = `http://localhost:5002/api/oauth/authorize/gmail?user_id=${userId}&redirect_uri=${encodeURIComponent(redirectUri)}`;
    } catch (error) {
      console.error('Error starting Gmail OAuth flow:', error);
      setError('Failed to start Gmail authentication process.');
      setLoading(false);
    }
  };

  // Revoke Gmail access
  const handleRevoke = async () => {
    setLoading(true);
    setError('');
    
    try {
      const response = await axios.post(`http://localhost:5002/api/oauth/revoke/gmail?user_id=${userId}`);
      
      if (response.data.status === 'success') {
        setAuthStatus({ authenticated: false, message: 'Gmail access revoked.' });
        if (onAuthSuccess) onAuthSuccess(false);
      } else {
        setError(response.data.message || 'Failed to revoke Gmail access.');
      }
    } catch (error) {
      console.error('Error revoking Gmail access:', error);
      setError('Failed to revoke Gmail access.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'flex-start' }}>
      {error && <Alert severity="error">{error}</Alert>}
      
      {authStatus.authenticated ? (
        <>
          <Alert severity="success">
            Connected to Gmail! Your AI clone can now access and respond to your emails.
          </Alert>
          <Button
            variant="outlined"
            color="error"
            onClick={handleRevoke}
            disabled={loading}
            startIcon={loading ? <CircularProgress size={20} /> : <EmailIcon />}
          >
            Disconnect Gmail
          </Button>
        </>
      ) : (
        <>
          <Typography variant="body1" gutterBottom>
            Connect your Gmail account to allow your AI clone to read and respond to your emails.
          </Typography>
          <Button
            variant="contained"
            color="primary"
            onClick={handleAuth}
            disabled={loading}
            startIcon={loading ? <CircularProgress size={20} /> : <EmailIcon />}
          >
            Connect Gmail
          </Button>
        </>
      )}
    </Box>
  );
};

export default GmailOAuthButton;
