import React, { useState } from 'react';
import { Box, Typography, Paper, Button, TextField, Tabs, Tab, Alert, CircularProgress, Divider } from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import MessageIcon from '@mui/icons-material/Message';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';
import axios from '../api/axios';
import GmailOAuthButton from './GmailOAuthButton';

// TabPanel component for tab content
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}

const DataSourceIntegration = ({ onDataAdded }) => {
  const [activeTab, setActiveTab] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [status, setStatus] = useState({ type: '', message: '' });
  
  // iMessage integration
  const [imessageStatus, setImessageStatus] = useState('idle'); // idle, loading, success, error
  const [imessageDays, setImessageDays] = useState(365); // Default to 1 year
  
  // LinkedIn integration
  const [linkedinInput, setLinkedinInput] = useState('');
  const [linkedinStatus, setLinkedinStatus] = useState('idle'); // idle, loading, success, error
  
  // Gmail integration
  const [gmailStatus, setGmailStatus] = useState('idle'); // idle, success, error
  const [isExtractingEmails, setIsExtractingEmails] = useState(false);
  const [emailDays, setEmailDays] = useState(30);
  const [emailMaxCount, setEmailMaxCount] = useState(100);
  const [emailExtractionResult, setEmailExtractionResult] = useState(null);
  
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
    setStatus({ type: '', message: '' });
  };
  
  // Handle email extraction
  const handleExtractEmails = async () => {
    setIsExtractingEmails(true);
    setEmailExtractionResult(null);
    
    try {
      const response = await axios.post('/api/oauth/gmail/extract', {
        days: emailDays,
        max_count: emailMaxCount,
        user_id: 'default' // Use default user ID or pass from props if multi-user
      });
      
      setEmailExtractionResult(response.data);
      
      // Show success status
      if (response.data.success) {
        setStatus({
          type: 'success',
          message: response.data.message || 'Successfully extracted emails!'
        });
        
        // Notify parent component if emails were extracted successfully
        if (onDataAdded) {
          onDataAdded({
            source: 'email',
            count: response.data.email_count || 0
          });
        }
      } else {
        setStatus({
          type: 'error',
          message: response.data.message || 'Failed to extract emails.'
        });
      }
    } catch (error) {
      console.error('Error extracting emails:', error);
      setEmailExtractionResult({
        success: false,
        message: error.response?.data?.message || error.message
      });
      
      setStatus({
        type: 'error',
        message: `Error extracting emails: ${error.response?.data?.message || error.message}`
      });
    } finally {
      setIsExtractingEmails(false);
    }
  };
  
  const handleImessageAccess = async () => {
    setImessageStatus('loading');
    setStatus({ type: '', message: '' });
    
    console.log('Attempting to extract iMessage data...');
    console.log('Days parameter:', imessageDays);
    
    try {
      // First try the regular endpoint
      try {
        console.log('Trying regular iMessage endpoint...');
        const response = await axios.post('/api/integrate/imessage', {
          days: imessageDays
        });
        
        console.log('iMessage API response:', response.data);
        
        if (response.data.success) {
          setImessageStatus('success');
          setStatus({
            type: 'success',
            message: `Successfully processed ${response.data.message_count} messages from iMessage (last ${imessageDays} days)!`
          });
          
          if (onDataAdded) {
            onDataAdded({
              source: 'imessage',
              count: response.data.message_count
            });
          }
          return; // Exit early if successful
        } else {
          // Handle error response from server
          console.error('iMessage extraction failed:', response.data);
          throw new Error(response.data.message || response.data.error || 'Failed to access iMessage data.');
        }
      } catch (mainError) {
        // If the regular endpoint fails, try the test endpoint
        console.log('Regular endpoint failed, trying test endpoint...');
        try {
          const testResponse = await axios.post('/api/integrate/imessage-test', {
            days: imessageDays
          });
          
          console.log('iMessage test API response:', testResponse.data);
          
          if (testResponse.data.success) {
            // Test endpoint worked, but regular endpoint failed
            setImessageStatus('error');
            setStatus({
              type: 'warning',
              message: `Test endpoint succeeded but regular endpoint failed. This suggests the backend is running but cannot access the iMessage database. Error: ${mainError.message}`
            });
          } else {
            throw new Error(testResponse.data.message || 'Both regular and test endpoints failed');
          }
        } catch (testError) {
          // Both endpoints failed
          console.error('Both endpoints failed:', { mainError, testError });
          throw mainError; // Re-throw the original error
        }
      }
    } catch (error) {
      // Handle network or other errors
      console.error('iMessage extraction error:', error);
      console.error('Error details:', {
        message: error.message,
        response: error.response?.data,
        status: error.response?.status
      });
      
      setImessageStatus('error');
      setStatus({
        type: 'error',
        message: error.response?.data?.message || 
                error.response?.data?.error || 
                `Network error: ${error.message}. Please check if the backend server is running.`
      });
    }
  };
  
  const handleLinkedinSubmit = async (e) => {
    e.preventDefault();
    
    if (!linkedinInput.trim()) {
      setStatus({
        type: 'error',
        message: 'Please enter your LinkedIn profile URL or paste your profile content.'
      });
      return;
    }
    
    setIsLoading(true);
    setLinkedinStatus('loading');
    setStatus({ type: '', message: '' });
    
    try {
      const response = await axios.post('/api/integrate/linkedin', {
        linkedin_data: linkedinInput
      });
      
      if (response.data.success) {
        setLinkedinStatus('success');
        setStatus({
          type: 'success',
          message: 'Successfully processed LinkedIn data! Your AI clone now understands your professional background.'
        });
        setLinkedinInput('');
        
        if (onDataAdded) {
          onDataAdded({
            source: 'linkedin',
            data: response.data
          });
        }
      } else {
        setLinkedinStatus('error');
        setStatus({
          type: 'error',
          message: response.data.error || 'Failed to process LinkedIn data.'
        });
      }
    } catch (error) {
      setLinkedinStatus('error');
      setStatus({
        type: 'error',
        message: error.response?.data?.error || 'Failed to process LinkedIn data. Please try again.'
      });
    } finally {
      setIsLoading(false);
    }
  };
  
  return (
    <Box>
      <Tabs 
        value={activeTab} 
        onChange={handleTabChange}
        variant="fullWidth"
        indicatorColor="primary"
        textColor="primary"
        sx={{ mb: 2 }}
      >
        <Tab 
          icon={<MessageIcon />} 
          label="iMESSAGE" 
          iconPosition="start"
        />
        <Tab 
          icon={<LinkedInIcon />} 
          label="LINKEDIN" 
          iconPosition="start"
        />
        <Tab 
          icon={<EmailIcon />} 
          label="GMAIL" 
          iconPosition="start"
        />
      </Tabs>
      
      {/* Status message */}
      {status.type && (
        <Alert 
          severity={status.type} 
          sx={{ mb: 2 }}
          onClose={() => setStatus({ type: '', message: '' })}
        >
          {status.message}
        </Alert>
      )}
      
      {/* iMessage Tab */}
      <TabPanel value={activeTab} index={0}>
        <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Extract Messages from iMessage
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            This will extract your sent messages from the Mac Messages app to help train your AI clone.
          </Typography>
          
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Number of days to extract:
            </Typography>
            <TextField
              type="number"
              value={imessageDays}
              onChange={(e) => setImessageDays(Math.max(1, parseInt(e.target.value) || 1))}
              fullWidth
              variant="outlined"
              size="small"
              InputProps={{ inputProps: { min: 1, max: 3650 } }}
              helperText="How far back to extract messages (1-3650 days)"
            />
          </Box>
          
          <Button
            variant="contained"
            color="primary"
            onClick={handleImessageAccess}
            disabled={imessageStatus === 'loading'}
            startIcon={imessageStatus === 'loading' ? <CircularProgress size={20} /> : <MessageIcon />}
            fullWidth
          >
            {imessageStatus === 'loading' ? 'Processing...' : 'Extract iMessage Data'}
          </Button>
          
          {imessageStatus === 'success' && (
            <Alert severity="success" sx={{ mt: 2 }}>
              iMessage data extracted successfully!
            </Alert>
          )}
        </Paper>
      </TabPanel>
      
      {/* LinkedIn Tab */}
      <TabPanel value={activeTab} index={1}>
        <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Add your LinkedIn profile
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            Add your LinkedIn profile URL to help your AI clone understand your professional background.
          </Typography>
          
          <form onSubmit={handleLinkedinSubmit}>
            <TextField
              label="LinkedIn Profile URL or Content"
              value={linkedinInput}
              onChange={(e) => setLinkedinInput(e.target.value)}
              fullWidth
              variant="outlined"
              multiline
              rows={4}
              placeholder="https://www.linkedin.com/in/your-profile-name/"
              sx={{ mb: 2 }}
              helperText="Enter your LinkedIn profile URL (e.g., https://www.linkedin.com/in/your-name/)"
            />
            
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={isLoading}
              startIcon={linkedinStatus === 'loading' ? <CircularProgress size={20} /> : <LinkedInIcon />}
              fullWidth
            >
              {linkedinStatus === 'loading' ? 'Processing LinkedIn Profile...' : 'Process LinkedIn Data'}
            </Button>
            
            {linkedinStatus === 'success' && (
              <Alert severity="success" sx={{ mt: 2 }}>
                LinkedIn profile data extracted successfully!
              </Alert>
            )}
          </form>
        </Paper>
        
        <Typography variant="body2" color="text.secondary" sx={{ mt: 2, fontStyle: 'italic' }}>
          Note: The LinkedIn scraper will extract your profile information including your experience, education, and skills.
          This process may take a minute or two as it navigates through your profile.
        </Typography>
      </TabPanel>
      
      {/* Gmail Tab */}
      <TabPanel value={activeTab} index={2}>
        <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Connect Gmail Account
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            Connect your Gmail account to allow your AI clone to learn from and respond to your emails.
          </Typography>
          
          <GmailOAuthButton 
            onAuthSuccess={(success) => {
              setGmailStatus(success ? 'success' : 'idle');
              if (success) {
                setStatus({
                  type: 'success',
                  message: 'Successfully connected to Gmail! Your AI clone can now access your emails.'
                });
                
                if (onDataAdded) {
                  onDataAdded({
                    source: 'gmail',
                    connected: true
                  });
                }
              }
            }}
          />
          
          {gmailStatus === 'success' && (
            <Box sx={{ mt: 3 }}>
              <Divider sx={{ mb: 2 }} />
              <Typography variant="subtitle2" gutterBottom>
                Extract Emails for Training
              </Typography>
              <Typography variant="body2" color="text.secondary" paragraph>
                Choose how many emails to extract for training your AI clone.
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
                <TextField
                  label="Number of days"
                  type="number"
                  size="small"
                  value={emailDays}
                  onChange={(e) => setEmailDays(Math.max(1, parseInt(e.target.value) || 1))}
                  InputProps={{ inputProps: { min: 1, max: 365 } }}
                  helperText="How far back to look"
                  sx={{ width: '50%' }}
                />
                <TextField
                  label="Max emails"
                  type="number"
                  size="small"
                  value={emailMaxCount}
                  onChange={(e) => setEmailMaxCount(Math.max(10, parseInt(e.target.value) || 10))}
                  InputProps={{ inputProps: { min: 10, max: 1000 } }}
                  helperText="Maximum to extract"
                  sx={{ width: '50%' }}
                />
              </Box>
              <Button
                variant="outlined"
                color="primary"
                startIcon={isExtractingEmails ? <CircularProgress size={20} color="primary" /> : <EmailIcon />}
                fullWidth
                disabled={isExtractingEmails}
                onClick={handleExtractEmails}
              >
                {isExtractingEmails ? 'Extracting...' : 'Extract Emails'}
              </Button>
              
              {emailExtractionResult && (
                <Paper 
                  elevation={1} 
                  sx={{ 
                    p: 2, 
                    mt: 2, 
                    bgcolor: emailExtractionResult.success ? 'success.light' : 'error.light' 
                  }}
                >
                  <Typography variant="body2">
                    {emailExtractionResult.message}
                  </Typography>
                  {emailExtractionResult.success && emailExtractionResult.email_count && (
                    <Typography variant="body2" sx={{ mt: 1 }}>
                      Extracted {emailExtractionResult.email_count} emails
                    </Typography>
                  )}
                </Paper>
              )}
            </Box>
          )}
        </Paper>
        
        <Typography variant="body2" color="text.secondary" sx={{ mt: 2, fontStyle: 'italic' }}>
          Note: Your AI clone will only use your emails for training and responding. No email data is shared with third parties.
          You can revoke access at any time by clicking the "Disconnect Gmail" button.
        </Typography>
        
        <Alert severity="info" sx={{ mt: 2 }}>
          <Typography variant="body2">
            <strong>How it works:</strong> When you connect your Gmail account, your AI clone will be able to:
            <ul>
              <li>Learn your writing style from sent emails</li>
              <li>Understand your communication patterns</li>
              <li>Generate responses that match your tone and style</li>
              <li>Automatically respond to incoming emails (when enabled)</li>
            </ul>
          </Typography>
        </Alert>
      </TabPanel>
    </Box>
  );
};

export default DataSourceIntegration;
