import React, { useState, useEffect } from 'react';
import { AppBar, Toolbar, Typography, Button, Box } from '@mui/material';
import { Link as RouterLink, useNavigate, useLocation } from 'react-router-dom';
import DownloadIcon from '@mui/icons-material/Download';

const Navbar = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userName, setUserName] = useState('');
  
  useEffect(() => {
    // Check if user is logged in by looking for token in localStorage
    const token = localStorage.getItem('token');
    const userJson = localStorage.getItem('user');
    
    setIsLoggedIn(!!token);
    
    if (userJson) {
      try {
        const user = JSON.parse(userJson);
        setUserName(user.name || user.email);
      } catch (error) {
        console.error('Error parsing user data:', error);
      }
    }
    
    console.log('Auth state checked - Token exists:', !!token);
  }, [location.pathname]); // Re-check when route changes

  const handleLogout = () => {
    // Clear authentication token and user info
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    console.log('User logged out');
    
    // Update state
    setIsLoggedIn(false);
    setUserName('');
    
    // Redirect to login page
    navigate('/login');
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          AI Clone
        </Typography>
        <Box>
          <Button color="inherit" component={RouterLink} to="/dashboard">
            Dashboard
          </Button>
          <Button color="inherit" component={RouterLink} to="/how-it-works">
            How It Works
          </Button>
          <Button color="inherit" component={RouterLink} to="/training">
            Training
          </Button>
          <Button color="inherit" component={RouterLink} to="/chat">
            Chat
          </Button>
          <Button color="inherit" component={RouterLink} to="/feedback">
            Feedback
          </Button>
          <Button color="inherit" component={RouterLink} to="/auto-response">
            Auto-Response
          </Button>
          <Button 
            color="inherit" 
            component={RouterLink} 
            to="/download"
            startIcon={<DownloadIcon />}
            sx={{ 
              backgroundColor: 'rgba(255, 255, 255, 0.1)', 
              '&:hover': { backgroundColor: 'rgba(255, 255, 255, 0.2)' } 
            }}
          >
            Download App
          </Button>
          {isLoggedIn && (
            <>
              <Button color="inherit" disabled>
                {userName || 'User'}
              </Button>
              <Button color="inherit" onClick={handleLogout}>
                Logout
              </Button>
            </>
          )}
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
