import React, { useState, useEffect } from 'react';
import axiosInstance from '../api/axios';
import { 
  Container, 
  Typography, 
  Box, 
  CircularProgress, 
  Tabs, 
  Tab, 
  Paper,
  Alert,
  AlertTitle,
  FormControlLabel,
  Switch
} from '@mui/material';
import FeedbackInterface from '../components/FeedbackInterface';
import LearningExamples from '../components/LearningExamples';

const FeedbackPage = () => {
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [activeTab, setActiveTab] = useState(0);
  const [daysAgo, setDaysAgo] = useState(null); // null means all time
  const [modelVersion, setModelVersion] = useState("v1.4"); // Default to v1.4 model
  const [hideReviewed, setHideReviewed] = useState(true); // Default to hiding reviewed messages
  const [channelFilter, setChannelFilter] = useState("all"); // Default to showing all channels
  const [stats, setStats] = useState({
    total: 0,
    approved: 0,
    corrected: 0,
    rejected: 0
  });

  useEffect(() => {
    // Fetch messages from the chat history
    const fetchMessages = async () => {
      try {
        setLoading(true);
        const params = {};
        if (daysAgo) params.days_ago = daysAgo;
        if (modelVersion) params.model_version = modelVersion;
        params.hide_reviewed = hideReviewed.toString();
        if (channelFilter !== 'all') params.channel = channelFilter;
        
        const response = await axiosInstance.get('/api/feedback/conversations', { params });
        
        // The backend now returns a flat list of messages instead of conversations
        let responseMessages = response.data.messages || [];
        console.log(`Received ${responseMessages.length} messages from backend`);
        
        // Log message details for debugging
        responseMessages.forEach((message, index) => {
          console.log(`Message ${index + 1}:`);
          console.log(`- Message: "${message.text?.substring(0, 30)}..." (${message.timestamp})`);
          console.log(`- Channel: ${message.channel || 'unknown'}`);
          if (message.context) {
            console.log(`- Context: "${message.context.text?.substring(0, 30)}..."`);
          }
        });
        
        // Messages are already sorted by the backend (newest first)
        setMessages(responseMessages);
        setStats(response.data.stats || {
          total: responseMessages.length,
          approved: 0,
          corrected: 0,
          rejected: 0
        });
        setLoading(false);
      } catch (err) {
        console.error('Error fetching conversations:', err);
        setError('Failed to load conversations. Please try again later.');
        setLoading(false);
      }
    };

    fetchMessages();
  }, [daysAgo, modelVersion, hideReviewed, channelFilter]);

  const handleFeedback = async (messageId, feedbackType, correctedText = null) => {
    try {
      await axiosInstance.post('/api/feedback/submit', {
        message_id: messageId,
        feedback_type: feedbackType,
        corrected_text: correctedText
      });
      
      // Update local state to reflect the feedback
      setMessages(prevMessages => {
        return prevMessages.map(message => {
          if (message.id === messageId) {
            return {
              ...message,
              feedback: {
                type: feedbackType,
                corrected_text: correctedText
              }
            };
          }
          return message;
        });
      });

      // Update stats
      setStats(prevStats => {
        const newStats = { ...prevStats };
        
        // If this message already had feedback, decrement the previous count
        const prevMessage = messages.find(m => m.id === messageId);
        if (prevMessage && prevMessage.feedback) {
          const prevType = prevMessage.feedback.type;
          if (prevType === 'approved') newStats.approved--;
          else if (prevType === 'corrected') newStats.corrected--;
          else if (prevType === 'rejected') newStats.rejected--;
        }
        
        // Increment the new feedback type count
        if (feedbackType === 'approved') newStats.approved++;
        else if (feedbackType === 'corrected') newStats.corrected++;
        else if (feedbackType === 'rejected') newStats.rejected++;
        
        return newStats;
      });
    } catch (err) {
      console.error('Error submitting feedback:', err);
      setError('Failed to submit feedback. Please try again.');
    }
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleDateFilterChange = (value) => {
    setDaysAgo(value);
  };
  
  const handleModelVersionChange = (value) => {
    setModelVersion(value);
  };

  const handleHideReviewedChange = (event) => {
    setHideReviewed(event.target.checked);
  };

  const handleChannelFilterChange = (value) => {
    setChannelFilter(value);
  };

  if (loading) {
    return (
      <Container sx={{ mt: 4, textAlign: 'center' }}>
        <CircularProgress />
        <Typography variant="h6" sx={{ mt: 2 }}>
          Loading conversations...
        </Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" gutterBottom>
        AI Clone Feedback System
      </Typography>
      
      <Box sx={{ mb: 4 }}>
        <Alert severity="info">
          <AlertTitle>About the Feedback System</AlertTitle>
          This feedback system helps improve your AI clone by learning from your corrections. 
          When you approve, edit, or reject responses, the system collects this data to enhance 
          future responses. Over time, your clone will better match your communication style.
        </Alert>
      </Box>
      
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Tabs 
          value={activeTab} 
          onChange={handleTabChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
        >
          <Tab label="Review Conversations" />
          <Tab label="Learning Examples" />
        </Tabs>
      </Paper>
      
      {activeTab === 0 ? (
        <FeedbackInterface 
          messages={messages} 
          onFeedback={handleFeedback} 
          stats={stats}
          error={error}
          daysAgo={daysAgo}
          onDateFilterChange={handleDateFilterChange}
          modelVersion={modelVersion}
          onModelVersionChange={handleModelVersionChange}
          hideReviewed={hideReviewed}
          onHideReviewedChange={handleHideReviewedChange}
          channelFilter={channelFilter}
          onChannelFilterChange={handleChannelFilterChange}
        />
      ) : (
        <LearningExamples daysAgo={daysAgo} onDateFilterChange={handleDateFilterChange} />
      )}
    </Container>
  );
};

export default FeedbackPage;
